import React from 'react'
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';
import './../css/slider-animations.css';
import { Link } from "react-router-dom";
const content = [
	{
		title: 'A VACATION WONDERFUL',
		description:
		'Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum. Fusce dapibus, tellus ac cursus commodo, tortor mauris condimentum nibh, ut fermentum massa justo sit amet risus. Cras justo odio, dapibus ac facilisis.',
		button: 'View More',
		image: 'http://playa.ancorathemes.com/wp-content/uploads/2017/08/slide1.jpg',
		linkpage:'/WaterPark'
	},

	{
		title: 'GOOD PLACE FOR ENJOYMENT',
		description:
		'Nullam id dolor id nibh ultricies vehicula ut id elit. Cras mattis consectetur purus sit amet fermentum. Morbi leo risus, porta ac consectetur ac, vestibulum at eros. Donec sed odio dui.',
		button: 'Discover',
		image: 'https://images.pexels.com/photos/3453009/pexels-photo-3453009.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=1700',
		linkpage:'/WaterPark'
	},
	{
		title: 'EXPERIENCE ENJOYMENT',
		description:
		'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in, egestas eget quam. Duis mollis, est non commodo luctus, nisi erat porttitor ligula.',
		button: 'View now',
		image: 'https://images.unsplash.com/photo-1559956144-ee11501d5459?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2100&q=80',
		linkpage:'/WaterPark'
	}
];
function SlideHome() {
    return (
        <div>
            <Slider className="slider-wrapper" autoplay={3000}>
			{content.map((item, index) => (
				<div
					key={index}
					className="slider-content"
					style={{ background: `url('${item.image}') no-repeat center center` }}
				>
					<div className="inner">
						<h1>{item.title}</h1>
						<p>{item.description}</p>
						<Link to={item.linkpage}><button className="SlideButton">{item.button}</button></Link>
					</div>
				</div>
			))}
		</Slider>
        </div>
    )
}

export default SlideHome;